@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
body {
  margin: 0;
  font-family: "Open Sans", "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
}

.app {
  padding: 1rem;
}
.app__header {
  display: flex;
  justify-content: space-between;
}
.app__header > h3 {
  text-transform: uppercase;
  color: rgba(228, 6, 6, 0.938);
  font-weight: 500;
  margin: 1rem 0;
}

.app__right,
.app__chart {
  margin-top: 1rem;
}
.select__country {
  width: 8rem;
  height: 50px;
  margin: 1rem 0;
}
.select__country > div {
  font-family: "Open Sans", "Courier New", Courier, monospace !important;
  font-size: 0.9rem;
}

.app__footer > p {
  text-align: center;
  margin: 1rem;
  font-size: 0.7rem;
}

@media screen and (min-width: 842px) {
  .app__container {
    display: flex;
    justify-content: space-between;
  }

  .app__left {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: space-between;
  }

  .app__right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 1rem;
    margin-top: 0;
    width: 40vw;
  }
  .app__chart {
    margin-top: 1rem;
  }

  .app__infoCard {
    display: flex;
    justify-content: space-between;
  }
}

@media screen and (min-width: 990px) {
  .app__header > h3 {
    font-size: 1.7rem;
  }
  .app__container {
    margin: 1.5rem 0;
  }
  .app__right {
    width: 30vw;
  }
}

.map__container {
  background-color: #ffffff;
  padding: 1rem;
  border-radius: 4px;
  box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}

.map__container .leaflet-container {
  height: 45vh;
}

.leaflet-popup-content p {
  margin: 0 !important;
  font-family: inherit;
}

@media screen and (min-width: 842px) {
  .map__container .leaflet-container {
    height: 66vh;
  }
}

.infoCard {
  padding: 1rem;
  cursor: pointer;
  margin-bottom: 0.7rem;
}

.infoCard__Cases {
  background-color: #dc293f !important;
}

.infoCard__Recovered {
  background-color: #008900 !important;
}

.infoCard__Deaths {
  background-color: #c20000 !important;
}
.infoCard--selected {
  border: 4px solid rgba(0, 0, 0, 0.25) !important;
}

.infoCard__todayCases {
  text-align: center;
  color: #fff;
  font-family: inherit !important;
}

.infoCard__totalCases {
  font-family: "Open Sans" !important;
  color: rgba(255, 255, 255, 0.9);
  text-align: center;
  font-size: 0.8rem !important;
}

@media screen and (min-width: 842px) {
  .infoCard {
    margin: 0;
  }
}

@media screen and (min-width: 990px) {
  .infoCard {
    padding: 2rem;
  }
}

.table-wrapper {
  height: 50vh;
  background-color: #fff;
  overflow-y: scroll;
}

.table-wrapper > h4 {
  padding: 0 0.7rem;
  margin-bottom: 0;
}

table {
  table-layout: fixed;
  width: 100%;
  word-wrap: break-word;
  border-spacing: 0;
}

th {
  padding: 1rem 0;
  font-size: 0.8rem;
}
td {
  text-align: center;
  padding: 0.7rem;
  font-size: 0.8rem;
}

tbody tr:nth-last-of-type(even) {
  background-color: rgb(224, 224, 224);
}

@media screen and (min-width: 990px) {
  .table-wrapper {
    height: 52vh;
  }
}

.chart__container {
  height: 30vh;
}
.chart__container > h4 {
  margin: 0.3rem 0;
}

.chartjs-render-monitor {
  height: 25vh !important;
}

@media screen and (min-width: 990px) {
  .chart__container {
    height: 30vh;
  }
  .chartjs-render-monitor {
    height: 25vh !important;
  }
}

